  async receive(from: string, message: Message) {
    console.log(" PM  ChatViewController receive*** * **  * * * * * ** * : ");
    console.log("PM  ChatViewController receive ", message);

    if (getName(MessageEnum.RestAPIResponceMessage) === message.id) {
      console.log("PM ChatViewController receive RestAPIResponceMessage");
      const apiRequestCallId = message.getData(
        getName(MessageEnum.RestAPIResponceDataMessage)
      );
      const responseJson = message.getData(
        getName(MessageEnum.RestAPIResponceSuccessMessage)
      );
      const errorResponse = message.getData(
        getName(MessageEnum.RestAPIResponceErrorMessage)
      );
      if (errorResponse) this.parseApiCatchErrorResponse(errorResponse);
      if (responseJson?.errors) this.parseApiErrorResponse(responseJson);

      if (responseJson) {
        if (apiRequestCallId === this.getChatApiCallId) {
          if (responseJson.data) {
            const chatData = responseJson.data;
            const { muted } = chatData.attributes.accounts_chats.find(
              (item: { attributes: { account_id: number } }) =>
                item.attributes.account_id === this.state.accountId
            ).attributes;
            this.setState({
              chatData,
              muted,
            });
            this.updateReadMessages();
          }
        }
        else if (
          apiRequestCallId === this.sendMessageApiCallId
        ) {
          console.log("get chatList API response sendMessageApiCallId ", responseJson);
          this.getChatList();
        }
        else if (
          apiRequestCallId === this.getChatListApiCallId
        ) {
          if (responseJson.data) {
            this.setState(
              {
                messageList: responseJson.data
              },
              () => {
              //(!this.state.conversationID && this.state.messageList.length > 0) {
                  //This function call one time after the message list  
                  // this.conversationID = "" + this.state.messageList[0].attributes.conversation_id
                  this.getReadMessage();
                //}
              }
            );
          }
        }
        else if (
          apiRequestCallId === this.unlockMessageApiCallId
        ) {
          if (responseJson.data) {
            this.getChatList();
          }else{
            this.showAlert(
              configJSON.errorTitle,
              responseJson.message,
              ""
            );
          }
        }
        else if (
          apiRequestCallId === this.addUserToChatApiCallId
        ) {
          this.hideModal();
        }
        else if (apiRequestCallId === this.toggleMuteApiCallId) {
          console.log("toggleMuteApiCallId ")
        }
        else if (
          apiRequestCallId === this.putReadMessageApiCallId
        ) {
          //Callback message for conversation screen 
          console.log("putReadMessageApiCallId call NavigationMessage")
          this.refreshConversationListScreen()
        }
        else if (apiRequestCallId === this.leaveChatApiCallId) {
          this.props.navigation.goBack();
        }
      }
    }
    else if (getName(MessageEnum.SessionResponseMessage) === message.id) {
      console.log("PM  ChatViewController receive SessionResponseMessage");
      const token: string = message.getData(
        getName(MessageEnum.SessionResponseToken)
      );
      runEngine.debugLog("PM ChatViewController receive TOKEN", token);
      const responseData: string = message.getData(
        getName(MessageEnum.SessionResponseData)
      );
      runEngine.debugLog("PM ChatViewController receive responseData", responseData);
      if (responseData) {
        const messageData = JSON.parse(responseData);
        if (messageData && messageData.meta) {
          const accountId: number = messageData.meta.id;
          this.setState({ accountId });
        }
      }
    }
  }

